import React, { Component } from 'react'
import Loader from 'react-loader-spinner'

const apiKey = process.env.REACT_APP_NASA_KEY
class FetchMars extends Component {
    
        state = {
            marsImgs: [],
            loading: false
        }

    componentDidMount() {
        this.setState({ loading: false })
        fetch(`https://api.nasa.gov/mars-photos/api/v1/rovers/curiosity/photos?sol=1000&api_key=${apiKey}`)
            .then(res => res.json())
            .then(data => {
                this.setState({ marsImgs: data.photos, loading: true })
            })
    }



    render() {

        const { loading } = this.state
        const allImgs = this.state.marsImgs.map((item, i) => (
            <div key={i}>
                <div key={i}></div>
                {
                    (!loading && <div key={i}>
                        <Loader type="ThreeDots" color="#00BFFF" height="80" width="80" />
                    </div>) ||
                    <div key={i}>
                        <img className="solo-image" key={item.id} src={item.img_src} alt="Mars Images" width=" 400" />
                    </div>
                }
                
            </div>
            
        ))

        return (
            <>
                <div className="mars-images">{allImgs}</div>
            </>
        )
    }
}

export default FetchMars